import {closeModal, openModal} from './modal';

function forms(formsSelector) {
    const form = document.querySelector(formsSelector),
          nameInput = document.getElementById('name'),
          phoneInput = document.getElementById('tel');

    const message = {
          loading: `img/form/spinner.svg`,
          success: `We will call you back as soon as possible!`,
          failure: `Something went wrong...`
    };
    
    let nameInputRegEx = /[a-zА-Я\s]{2,12}/i, 
        phoneInputRegEx = /^((\+?375-?|8-?\s?0\s?))?\s?\(?0?(29|25|44|33)\)?-?\s?[1-9]\s?(\d{2}-?\s?){2}\d{2}$/;
    
    postData(form); 
    function postData(form) {
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            let isValidNameInput = nameInputRegEx.test(nameInput.value.trim()),
                isValidphoneInput = phoneInputRegEx.test(phoneInput.value.trim()); 

            if (!isValidNameInput) {
                clearError();
                showError(nameInput);
            }
            
            if (!isValidphoneInput) { 
                clearError();
                showError(phoneInput);
            }

            if (!isValidNameInput && !isValidphoneInput) {
                clearError();
                showError(nameInput);
                showError(phoneInput);
            }
            
            if (isValidNameInput && isValidphoneInput) sendRequest();
            
            function sendRequest () {
                let statusMessage = document.createElement('img');
                statusMessage.src = message.loading;
                statusMessage.classList.add('message');
                
                form.insertAdjacentElement('afterend', statusMessage);
            
                const request = new XMLHttpRequest();
                request.open('POST', 'https://reqres.in/api/users');
                request.setRequestHeader('Content-type', 'application/json');
                
                const formData = new FormData(form);

                const object = {};

                formData.forEach((value, key) => object[key] = value);
                const json = JSON.stringify(object);

                request.send(json);

                request.addEventListener('load', () => {
                    try {
                        let statusType = Math.round(request.status / 100);
                        if (statusType === 2) {
                            console.log(request.response);
                            showThanksModal(message.success);
                            statusMessage.remove();
                            form.reset();
                        } else {
                            showThanksModal(message.failure);
                            }
                    } catch {
                        console.error(request.status);
                        showThanksModal(message.failure);   
                    }  
                }); 

                request.onerror = () => {
                    console.error(request.status);
                    showThanksModal(message.failure);
                }; 
            }
        }, false);
    }

    function showError (input) {
        let error = document.createElement('div');
        let error2 = document.createElement('div');

        error.classList.add('error');
        error2.classList.add('error');

            if (input === nameInput) {
                nameInput.classList.add('invalid');
                error.innerHTML = `Type 2 to 12 letters, please`;
                nameInput.value = '';
            }
            if (input === phoneInput) {
                phoneInput.classList.add('invalid');
                error2.innerHTML = `Type your phone with code, please`;
                phoneInput.value = '';
            }
            if (input === nameInput && input === phoneInput) {
                input.classList.add('invalid');
                error.innerHTML = `Type 2 to 12 letters, please`;
                error2.innerHTML = `Type your phone with code, please`;
                input.value = '';
            }
            
            nameInput.insertAdjacentElement('beforeBegin', error);
            phoneInput.insertAdjacentElement('beforeBegin', error2);
        
            setTimeout(() => {
                nameInput.classList.remove('invalid');
                phoneInput.classList.remove('invalid');
                clearError();
            }, 3000);

        return false;
    }

    function clearError() {
        let errors = document.querySelectorAll('.error');
        errors.forEach(error => error.remove());
    }

    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog');

        prevModalDialog.classList.add('hide');
        openModal('.modal');

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div class="modal__content">
                <div class="modal__close" data-close>×</div>
                <div class="modal__title">${message}</div>
            </div>
        `;
        document.querySelector('.modal').appendChild(thanksModal);

        setTimeout(() => {
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal('.modal');
        }, 3000);
    }
}

export default forms;